

@media only screen and (min-width: 1008px) {
    .grandArea {
        flex-direction: row !important;
        margin-top: 1rem !important;
    }

    ._area_teclado {
        align-items: flex-start !important;
        position: relative;
        top:1rem;
    }

    .webcam_area{
        height: 500px !important;
        width:  100% !important;
    }

    .cam{
        width: auto !important;
    }
    
}


.grandArea {
    display: flex;
    flex-direction: column;
    justify-items: cente;
    align-items: center;
    margin-top: 5rem;
}


.area_header_info_patient {
    width: auto;
    text-align: center;
    height: 100px;
    color: #fff;
    background-color: #37BBAA;
    user-select: none;
}

._area_photo_and_chapa{
   /* background-color: red;*/
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    gap:1rem;
    margin:0 auto;
    width: 50%;
}

._area_teclado {
   /* background-color: rgb(76, 95, 188);*/
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:1rem;
    margin:0 auto;
    width: 722px;

}

._area_inputs {
    position: relative;
    top:10px;
    display: flex;
    justify-content: space-between;
    width: 400px;
}

.btn_save {
    padding: 1rem;
    width: 400px;
    height: 65px;
    font-size: 30px;
}

.webcam_area{
    max-width: 525px;
    max-height: 430px;
    text-align: right;
}

.cam{
    width: 100%;
    height: 100%;
}

.input_chapa {
    padding: 0.8rem;
    font-size: 20px;
}

